.main {
    display: flex;
    justify-content: center;
}

.box{
    max-width:500px;
    border:2px solid red;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:10px;
    
    img{
        width:80%;
    }

    h2{
        
    }
}

button{
    background:coral;
    border-radius: 5px;
    border:1px solid black;
    margin:4px;
    padding:10px 20px;
    cursor: pointer;
    &:hover{
        background:bisque;
        border:1px solid black;
    }
}

.tie{
    border:5px solid grey;
}

.lose {
    border:5px solid grey;
    
    
}
.win{
    border:10px solid red;
}

.resultP{
    font-size: 80px;
    font-weight: bold;
    text-transform: uppercase;
}